.hero{
    width: 100%;
    min-height: 100vh;
    background:linear-gradient(rgba(8,0,58,0.7),rgba(8,0,58,0.7)),url("../../img/hero.png");
    background-size: cover;
    background-position: center;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-text{
    text-align: center;
    max-width: 800px;
}

.hero-text h1{
    font-style: 60px;
    font-weight: 600;
}

.hero-text p{
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4;
}

@media (max-width:840px){
    .hero-text h1{
        font-size: 40px;
    }
}

@media (max-width:650px){
    .hero-text h1{
        font-size: 30px;
        margin: auto;
        max-width: 400px;
    }

    .hero-text p{
        font-size: 14px;
        margin: 15px auto 30px;
    }
}